*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI',
    'Microsoft JhengHei', Roboto, 'Helvetica Neue', Arial, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
p {
  line-height: 1.5;
}

h1,
.h1 {
  font-size: 4rem;
  font-weight: bold;
  @include breakpoint(pad) {
    font-size: 3rem;
  }
  @include breakpoint(phone) {
    font-size: 2rem;
  }
}

h2,
.h2 {
  font-size: 3rem;
  @include breakpoint(pad) {
    font-size: 2rem;
  }
  @include breakpoint(phone) {
    font-size: 1.5rem;
  }
}

h3,
.h3 {
  font-size: 2.25rem;
  @include breakpoint(pad) {
    font-size: 1.25rem;
  }
  @include breakpoint(phone) {
    font-size: 1rem;
  }
}

h4,
.h4 {
  font-size: 2rem;
  @include breakpoint(pad) {
    font-size: 1.5rem;
  }
  @include breakpoint(phone) {
    font-size: 1rem;
  }
}

h5,
.h5 {
  font-size: 1.5rem;
  @include breakpoint(pad) {
    font-size: 1.25rem;
  }
}

.font {
  &-xs {
    font-size: 14px;
  }
  &-sm {
    font-size: 20px;
    @include breakpoint(pad) {
      font-size: 16px;
    }
  }
}

a {
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

select {
  height: 46px;
  margin-left: 20px;
  font-size: 18px;
  color: #495057;
  padding: 10px 0 10px 19px;
  display: block;
  min-height: 1.2em;
  @include breakpoint(pad) {
    font-size: 16px;
    padding: 12px 0 12px 16px;
  }
  @include breakpoint(phone) {
    font-size: 14px;
    padding: 8px 0 8px 16px;
  }
}

.text {
  &-center {
    text-align: center;
  }
  &-bold {
    font-weight: 900;
  }
  &-danger {
    color: $danger;
  }
  &-white {
    color: $white;
  }
  &-primary {
    color: $primary;
  }
}

.bg {
  &-primary {
    background-color: $primary;
  }
  &-pink {
    background-color: $pink;
  }
  &-danger {
    background-color: $danger;
  }
}

.container {
  max-width: 1110px;
  margin: 0 auto;
  padding: 64px 10px 80px;
  @include breakpoint(pad) {
    max-width: 690px;
    padding: 40px 0;
  }
  @include breakpoint(phone) {
    width: 100%;
    padding: 32px 5px;
  }
}

.title {
  font-weight: bold;
  @include breakpoint(pad) {
    margin-bottom: 32px;
  }
  @include breakpoint(phone) {
    margin-bottom: 20px;
  }
}
