.nav {
  padding: 30px 56px;
  align-items: center;
  @include breakpoint(pad) {
    padding: 30px 19px;
  }
  @include breakpoint(phone) {
    padding: 0;
    &_list {
      width: 100%;
    }
  }
  &_brand {
    padding: 0 32px;
    @include breakpoint(pad) {
      padding: 0 20px;
    }
    @include breakpoint(phone) {
      margin: 10px auto;
      margin-bottom: 10.86px;
    }
  }
  &_item {
    padding: 0 32px;
    font-size: 20px;
    @include breakpoint(pad) {
      padding: 0 20px;
    }
    @include breakpoint(phone) {
      margin: 0;
      border: 1px solid $white;
      width: 50%;
      text-align: center;
    }
    a {
      color: $white;
      display: block;
      padding: 8px 0;
    }
    &:hover {
      background-image: linear-gradient(0deg, $danger 50%, transparent 50.1%);
    }
    @include breakpoint(phone) {
      &:hover {
        background-color: $danger;
      }
    }
  }
}
